<template>
  <!-- 测试页面 -->
  <div class="test">
    <el-card class="box-card bottom-card" :body-style="{ padding: '0px'}">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('commom.address') }}</span>
      </div>
      <!-- <div v-for="address in whiteList" :key="address.index" class="address">{{address}}</div> -->
      <div>
        <el-table :data="whiteList" style="width: 100%" height="280" size="mini">
          <el-table-column label="Address" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.table.address }} </span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Test',
  data () {
    return {
      whiteList: [
      ]
    }
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
.test {
  width: 1200px;
  margin: 400px auto;
}
</style>



